$mainColor: #ff0000;


.title {
  color: pink;
  background-color: $mainColor;
}

.my-alert-grey {
  background: #eeeeee;
  padding: 15px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;

  p {
    margin: 0;
    padding: 0;
  }
}

.my-alert-red {
  @extend .my-alert-grey;
  background: #21af6d;

  p {
    margin: 0;
    padding: 0;
  }
}

@mixin box() {
  background: #eeeeee;
  padding: 15px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.my-alert-blue {
  @include box()
}